/* ExpenseTracker.module.css */

.expenseTracker {
    font-family: Arial, sans-serif;
    background: linear-gradient(to right, #ffecd2, #fcb69f);
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
}

/* Adjust the container styles for better alignment and spacing */
.expenseTracker .container {
    background-color: white;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 800px; /* Increase the max-width for better spacing */
    width: 100%;
    margin: 2rem auto; /* Center align the container */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.expenseTracker h1 {
    margin-bottom: 1rem;
    color: #333;
}

.expenseTracker .balance {
    background-color: #f9f9f9; /* Light background color */
    padding: 1rem; /* Padding around the content */
    border-radius: 8px; /* Rounded corners */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow */
    text-align: center; /* Center align text */
    margin-bottom: 2rem;
}

.expenseTracker .balance h2 {
    font-size: 2.5rem; /* Larger font size for balance */
}

.expenseTracker .summary {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;
    width: 100%;
}

.expenseTracker .income, .expenseTracker .expenses {
    flex: 1;
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    text-align: center;
    margin: 0 0.5rem;
    background-color: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.expenseTracker .income:hover, .expenseTracker .expenses:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.expenseTracker .income h4, .expenseTracker .expenses h4 {
    margin-bottom: 0.5rem;
    font-size: 1.5rem;
    color: #333;
}

.expenseTracker .income p, .expenseTracker .expenses p {
    font-size: 1.25rem;
    color: #4caf50;
}

.expenseTracker .expenses p {
    color: #f44336;
}

.expenseTracker .addTransaction {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 2rem;
}

.expenseTracker .addTransaction input[type="text"],
.expenseTracker .addTransaction input[type="number"] {
    padding: 0.5rem;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.expenseTracker .addTransaction input[type="radio"] {
    margin: 0 0.5rem;
}

.expenseTracker .addTransaction label {
    margin-right: 1rem;
}

.expenseTracker .addTransaction button {
    padding: 0.75rem;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.expenseTracker .addTransaction button:hover {
    background-color: #45a049;
}

.expenseTracker .profile {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.expenseTracker .profilePhoto {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.expenseTracker .signOutButton {
    padding: 0.5rem 1rem;
    background-color: #f44336;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.expenseTracker .signOutButton:hover {
    background-color: #e53935;
}

.expenseTracker .transactions {
    background-color: white;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 2rem;
    max-width: 800px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.expenseTracker .transactions h3 {
    margin-bottom: 1rem;
    font-size: 1.5rem;
    color: #333;
}

.expenseTracker .transactions ul {
    list-style: none;
    padding: 0;
    width: 100%;
}

.expenseTracker .transactions li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ddd;
}

.expenseTracker .transactions li:last-child {
    border-bottom: none;
}

.expenseTracker .transactions li h4 {
    margin: 0;
}

.expenseTracker .transactions li p {
    display: flex;
    align-items: center;
    margin: 0;
}

.expenseTracker .transactions li .deleteIcon {
    margin-left: 20px;
    cursor: pointer;
}
